import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { APP_ROUTE } from '@core/constants';
import { ProfileApi } from '@core/data-access/apis/profile.api';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'findqo-unsubscribe-email',
    templateUrl: './unsubscribe-email.component.html',
    styleUrl: './unsubscribe-email.component.scss',
    standalone: false
})
export class UnsubscribeEmailComponent implements OnInit {
	constructor(
		private readonly profileApi: ProfileApi,
		private readonly activatedRoute: ActivatedRoute,
		@Inject(PLATFORM_ID) public platformId
	) {}

	readonly appRoutes = APP_ROUTE;
	isUnsubscribed = false;
	token = '';

	unSubscribe(): void {
		this.profileApi.unSubscribeEmail({ token: this.token }).subscribe(
			() => {
				this.isUnsubscribed = true;
			},
			() => {
				this.isUnsubscribed = false;
			}
		);
	}

	ngOnInit(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.token = this.activatedRoute.snapshot.queryParams?.['token'];
		}
	}
}
